/* ----- Dependencies and styles -------*/

import * as React from 'react'
import { CSSProperties } from 'react'
import ReactAce from 'react-ace';
import { Button, ButtonGroup } from '@material-ui/core'

import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-min-noconflict/mode-javascript"
import "ace-builds/src-min-noconflict/mode-java"
import "ace-builds/src-noconflict/theme-dracula";
import "ace-builds/src-noconflict/ext-language_tools"

export interface StylesDictionary{
    [Key: string]: CSSProperties;
}

const styles:StylesDictionary = {
    editor: {
        float: 'right',
        height: '500px',
        marginRight: '50px',
        width: '800px',
    },
    btn: {
        transform: 'translate(108px, 525px)',
        backgroundColor: '#5e60ce',
        color: '#fff',
    },
    btnOpc: {
        transform: 'translate(800px, 105px)',
    },
}

/* ----- Main class ------ */

interface ILanguageProps {
    language: string
}

export default class Editor extends React.Component<ILanguageProps>{
    
    onChange(newValue: any, e: any) {
        console.log(newValue, e);
    }
    
    public render(){
        var { language } = this.props
        return(
            <div id="editor">
                <ReactAce style = {styles.editor}
                    mode={language}
                    theme="dracula"
                    onChange={this.onChange}
                    name="UNIQUE_ID_OF_DIV"
                    editorProps={{ $blockScrolling: true }}
                    setOptions={{
                        enableBasicAutocompletion: true,
                        enableLiveAutocompletion: true,
                        enableSnippets: true
                    }}
                />
                <Button style={styles.btn}>Run</Button>
                <ButtonGroup style={styles.btnOpc} variant="contained" color="primary" aria-label="contained primary button group">
                    <Button>Python</Button>
                    <Button>JS</Button>
                    <Button>Java</Button>
                </ButtonGroup>
            </div>
        )
    }
}